import React, { useRef, forwardRef, useState } from "react";
import { Form } from "@unform/web";
import * as Yup from "yup";
import { oneOf } from "prop-types";

import { Input, Radio } from "../NewInput";
import SquareButton from "../SquareButton";
import useAxios from "../../hooks/useAxios";

import styles from "./styles.module.scss";

const initialValues = { type: "b2c" };

const ContactForm = forwardRef(({ type }, ref) => {
  const formRef = useRef(null);
  const { exec, loading } = useAxios();

  const [typeValue, setTypeValue] = useState(initialValues.type);

  const handleSubmit = async (data) => {
    try {
      formRef.current.setErrors({});

      const schema = Yup.object().shape({
        name: Yup.string().min(3, "Nome inválido").required("Nome inválido"),
        email: Yup.string()
          .email("E-mail inválido")
          .required("E-mail inválido"),
        company:
          (type === "simple" || typeValue === "b2b") &&
          Yup.string().min(6, "Empresa inválida").required("Empresa inválida"),
        message: Yup.string()
          .min(20, "Mensagem inválida")
          .required("Mensagem inválida"),
      });

      await schema.validate(data, {
        abortEarly: false,
      });

      await exec({
        method: "post",
        url: "/contato",
        data: {
          nome: data.name,
          empresa: data.company,
          tipo: data.type === "b2c" ? "Empresa" : "Candidato",
          email: data.email,
          mensagem: data.message,
        },
      });

      formRef.current.reset();
    } catch (err) {
      const validationErrors = {};
      if (err instanceof Yup.ValidationError) {
        err.inner.forEach((error) => {
          validationErrors[error.path] = error.message;
        });
        formRef.current.setErrors(validationErrors);
      }
    }
  };

  const handleTypeChange = (value) => {
    setTypeValue(value);
  };

  return (
    <Form ref={formRef} onSubmit={handleSubmit} initialData={initialValues}>
      {type === "default" && (
        <div className={styles.itemWrapper}>
          <Radio
            name="type"
            options={[
              { id: "b2c", label: "Sou um candidato" },
              { id: "b2b", label: "Represento uma empresa" },
            ]}
            onChange={handleTypeChange}
          />
        </div>
      )}
      <div className={styles.itemWrapper}>
        <Input
          label="Nome "
          name="name"
          placeholder="Digite seu nome"
          type="text"
          disabled={loading}
        />
      </div>
      {(type === "simple" || typeValue === "b2b") && (
        <div className={styles.itemWrapper}>
          <Input
            label="Empresa"
            name="company"
            placeholder="Digite sua empresa"
            type="text"
            disabled={loading}
          />
        </div>
      )}
      <div className={styles.itemWrapper}>
        <Input
          label="E-mail"
          name="email"
          placeholder="Digite seu e-mail"
          type="text"
          disabled={loading}
        />
      </div>
      <div className={styles.itemWrapper}>
        <Input
          label="Mensagem"
          name="message"
          placeholder="Como podemos te ajudar?"
          type="textarea"
          disabled={loading}
        />
      </div>
      <div className={styles.buttonWrapper}>
        <SquareButton disabled={loading} text="Enviar" type="submit" />
      </div>
    </Form>
  );
});

ContactForm.propTypes = {
  type: oneOf(["default", "simple"]),
};

ContactForm.defaultProps = {
  type: "default",
};

export default ContactForm;
