import React, { useState, useEffect } from "react";

import { Row, Column } from "../../components/Grid";
import ItemParser from "../../components/ItemParser";
import HeroSlider from "../../components/HeroSlider";
import PaginationWrapper from "../../components/PaginationWrapper";
import SkeletonList from "../../components/SkeletonList";
import useAxios from "../../hooks/useAxios";

import SliderBannerA2x from "../../assets/images/home-slider-01@2x.png";

const sliderData = [
  {
    id: 1,
    title: "Se <b>#inklua</b> no mercado de trabalho",
    description:
      "Somos uma plataforma feita para que você explore vagas, conteúdos e se #inklua conosco.",
    image: SliderBannerA2x,
  },
];

const Home = () => {
  const [contentData, setContentData] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const { exec, loading } = useAxios();

  const fetchData = async () => {
    const response = await exec({
      method: "get",
      url: `/home?page=${currentPage}`,
    });

    if (response.error) {
      return;
    }

    setContentData([...contentData, ...response.data]);
  };

  useEffect(() => {
    fetchData();
  }, [currentPage]);

  const handlePagination = () => {
    setCurrentPage(currentPage + 1);
  };

  return (
    <>
      <HeroSlider items={sliderData} />
      {!contentData.length && currentPage === 1 && (
        <Row>
          <SkeletonList quantity={12} />
        </Row>
      )}
      {Boolean(contentData.length) && (
        <>
          <Row>
            {contentData.map((item) => (
              <Column key={`column_${item.id}`}>
                <ItemParser key={`item_${item.id}`} item={item} />
              </Column>
            ))}
            {loading && <SkeletonList quantity={6} />}
          </Row>
          <PaginationWrapper onPress={handlePagination} hidden={loading} />
        </>
      )}
    </>
  );
};

export default Home;
