import React, { forwardRef } from "react";
import { string, arrayOf, node } from "prop-types";

import SectionTitle from "../../components/SectionTitle";
import SectionIntro from "../../components/SectionIntro";
import ResponsiveImage from "../../components/ResponsiveImage";

import styles from "./styles.module.scss";

const ModuleOne = forwardRef(({ title, subtitle, description, image }, ref) => {
  const generateDescription = () => ({
    __html: description,
  });

  return (
    <section className={styles.wrapper} ref={ref}>
      <div>
        <SectionIntro level="span" text={title} />
        <SectionTitle level="h2" children={subtitle} />
        <div
          className={styles.descriptionWrapper}
          dangerouslySetInnerHTML={generateDescription()}
        />
      </div>
      <div>
        <ResponsiveImage
          className={styles.image}
          alt=""
          images={[image[0], image[1], image[2]]}
        />
      </div>
    </section>
  );
});

ModuleOne.propTypes = {
  title: string.isRequired,
  subtitle: string.isRequired,
  description: string.isRequired,
  image: arrayOf(node).isRequired,
};

export default ModuleOne;
