import React from "react";
import { string } from "prop-types";

import SquareLinkButton from "../../components/SquareLinkButton";
import { parseUrl } from "../../helpers";

import styles from "./styles.module.css";

const Advertising = ({ image, title, description, url }) => {
  return (
    <article className={styles.wrapper}>
      <span className={styles.label}>Patrocinado</span>
      <img src={image} className={styles.image} alt={`Imagem de ${title}`} />
      <h1 className={styles.title}>{title}</h1>
      <p className={styles.description}>{description}</p>
      <div className={styles.footer}>
        <SquareLinkButton
          type="outline"
          text="Saiba mais"
          rel="external"
          url={parseUrl(url)}
        />
      </div>
    </article>
  );
};

Advertising.propTypes = {
  image: string.isRequired,
  title: string.isRequired,
  description: string.isRequired,
  url: string.isRequired,
};

export default Advertising;
