import { useModalContext } from "../../contexts/Modal";
import SquareButton from "../../components/SquareButton";
import SocialMediaShareButton from "../../components/SocialMediaShareButton";
import { Plus } from "../../libs/icons";

import styles from "./styles.module.scss";

const ShareModal = () => {
  const { setOpen, isOpen, url, title } = useModalContext();

  const copyUrlToClipboard = () => {
    const ta = document.createElement("textarea");
    ta.innerText = url.startsWith("http") ? url : window.location.origin + url;
    document.body.appendChild(ta);
    ta.select();
    document.execCommand("copy");
    ta.remove();
  };

  return (
    <>
      <div
        aria-hidden={isOpen}
        className={[styles.wrapper, isOpen && styles.isOpen].join(" ")}
        onClick={() => setOpen(false)}
      />
      <div className={[styles.container, isOpen && styles.isOpen].join(" ")}>
        <button
          aria-label="Fechar"
          className={styles.closeButton}
          onClick={() => setOpen(false)}
        >
          Close <Plus />
        </button>
        <h1 className={styles.title}>{title}</h1>
        {url && (
          <p>
            <a className={styles.url} href={url} onClick={() => setOpen(false)}>
              {url.startsWith("http") ? url : window.location.origin + url}
            </a>
          </p>
        )}
        <div className={styles.actions}>
          <SquareButton text="Copiar link" onClick={copyUrlToClipboard} />
          <div className={styles.socialLinks}>
            <SocialMediaShareButton url={url} service="whatsapp" />
            <SocialMediaShareButton url={url} service="facebook" />
            <SocialMediaShareButton url={url} service="linkedin" />
            <SocialMediaShareButton url={url} service="twitter" />
          </div>
        </div>
      </div>
    </>
  );
};

export default ShareModal;
