import React from "react";
import { oneOf, node, string, bool } from "prop-types";

import styles from "./styles.module.scss";

const SectionTitle = ({ children, level, color, linebreak }) => {
  const renderContent = () => ({
    __html: children,
  });

  if (level === "h2") {
    return (
      <h2
        className={[styles.title, linebreak && styles.linebreak].join(" ")}
        style={{ color: color }}
        dangerouslySetInnerHTML={renderContent()}
      />
    );
  }

  if (level === "span") {
    return (
      <span
        className={[styles.title, linebreak && styles.linebreak].join(" ")}
        style={{ color: color }}
        dangerouslySetInnerHTML={renderContent()}
      />
    );
  }

  return (
    <p
      className={[styles.title, linebreak && styles.linebreak].join(" ")}
      style={{ color: color }}
      dangerouslySetInnerHTML={renderContent()}
    />
  );
};

SectionTitle.propTypes = {
  children: node.isRequired,
  level: oneOf(["h2", "span", "p"]).isRequired,
  color: string,
  linebreak: bool,
};

SectionTitle.defaultProps = {
  color: null,
  linebreak: true,
};

export default SectionTitle;
