import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";

import PositionDescription from "../../components/PositionDescription";
import useAxios from "../../hooks/useAxios";
import { toDate } from "../../helpers";

const Position = () => {
  const { id } = useParams();
  const [contentData, setContentData] = useState(null);
  const [isLoading, setLoading] = useState(true);
  const { exec, loading } = useAxios();

  const handleCompleoUrl = (url) => {
    return url.replace("/Visualizar/", "/PreCandidatar/");
  };

  const fetchData = async () => {
    const response = await exec({
      method: "get",
      url: `/vaga/${id}`,
    });

    setContentData(response);
    setLoading(false);
  };

  useEffect(() => {
    fetchData();
  }, []);

  if (isLoading || loading) {
    return null;
  }

  return (
    <PositionDescription
      title={contentData.title}
      date={toDate(contentData.date, "DD/MM/YYYY")}
      text={contentData.description}
      url={handleCompleoUrl(contentData.url)}
    />
  );
};

export default Position;
