import React, { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";

import { Blocks, Mail } from "../../libs/icons";
import IconLink from "../../components/IconLink";
import SearchField from "../../components/SearchField";
import SocialMediaLink from "../SocialMediaLink";
import { Instagram, Facebook, LinkedIn } from "../../libs/icons";
import Config from "../../config";

import Logo from "./logo-inklua-color.svg";
import styles from "./styles.module.scss";

const Header = () => {
  const [isFloating, setFloating] = useState(false);
  const location = useLocation();
  const useAlternative = location?.pathname.includes("para-empresas");

  const handleScroll = () => {
    setFloating(window.scrollY > 0);
  };

  const renderSpacing = () => (
    <span className={styles.spacer} aria-hidden="true" />
  );

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  if (useAlternative) {
    return (
      <>
        {renderSpacing()}
        <header
          className={[styles.wrapper, isFloating && styles.floating].join(" ")}
        >
          <div className={styles.container}>
            <Link to="/" aria-label="Início">
              <img src={Logo} alt="Inklua" />
            </Link>
            <div className={styles.socialMediaWrapper}>
              <SocialMediaLink url={Config.FACEBOOK_URL} color="#transparent">
                <Facebook color="#1b9b9e" />
              </SocialMediaLink>
              <SocialMediaLink url={Config.LINKEDIN_URL} color="#transparent">
                <LinkedIn color="#1b9b9e" />
              </SocialMediaLink>
              <SocialMediaLink url={Config.INSTAGRAM_URL} color="#transparent">
                <Instagram color="#1b9b9e" />
              </SocialMediaLink>
            </div>
          </div>
        </header>
      </>
    );
  }

  return (
    <>
      {renderSpacing()}
      <header
        className={[styles.wrapper, isFloating && styles.floating].join(" ")}
      >
        <div className={styles.container}>
          <a href="/" aria-label="Início">
            <img src={Logo} alt="Inklua" />
          </a>
          <SearchField />
          <div className={styles.links}>
            <IconLink text="Home" to="/" type="link">
              <Blocks color="#1B9B9E" />
            </IconLink>
            <IconLink text="Contato" to="/contato" type="link">
              <Mail color="#1B9B9E" />
            </IconLink>
          </div>
        </div>
      </header>
    </>
  );
};

Header.propTypes = {};

export default Header;
