import React, { useEffect, useState } from "react";
import { string } from "prop-types";

import { useModalContext } from "../../contexts/Modal";
import { Share } from "../../libs/icons";
import { getScreenSize } from "../../helpers";

import styles from "./styles.module.scss";

const ShareButton = ({ text, url, title }) => {
  const isHandHeld = getScreenSize() !== "desktop";
  const { setOpen, setUrl, setTitle } = useModalContext();
  const [isHovered, setHovered] = useState(false);

  const openModal = () => {
    setUrl(url);
    setTitle(title);
    setOpen(true);
  };

  useEffect(() => {
    return () => {
      setUrl(null);
      setTitle(null);
    };
  }, []);

  return (
    <button
      className={styles.button}
      onClick={openModal}
      type="button"
      aria-label={text}
      onMouseEnter={() => !isHandHeld && setHovered(true)}
      onMouseLeave={() => !isHandHeld && setHovered(false)}
    >
      {text}
      <Share color={isHovered ? "#fff" : "#1b9b9e"} />
    </button>
  );
};

ShareButton.propTypes = {
  text: string,
  url: string.isRequired,
  title: string.isRequired,
};

ShareButton.defaultProps = {
  text: "Compartilhar",
};

export default ShareButton;
